@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
.imge{margin-top: 2rem;
    height: 300px;
    width: 50%;
    border-radius: 9px;
    display: flex;
    align-self: flex-end;
    box-shadow: 0 20px 40px +14px rgba(8, 3, 11, 0.912);
}
.imge:hover{
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);
    
    
      box-shadow: 0 20px 40px +24px rgba(133, 104, 104, 0.25);}

.infocard{
    box-shadow: 0 20px 40px +14px rgba(0, 0, 0, 0.25);
    opacity:0.9;
    background: linear-gradient(to bottom, rgba(53, 42, 255, 0.181) 0%,rgba(33,33,32,1) 60%,rgba(33,33,32,1) 100%);
    background-color: rgb(3, 3, 3);
    border-color: #000000;
    border-radius: 8px;
    box-shadow: 0 20px 40px -14px rgba(255, 243, 243, 0.25);
    color: #a49a9a;
    margin: 2px;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    max-height: 35rem;
   max-width: 200rem;
  }
